.advantages__container {
  @include flex-center;
}

.advantages__list {
  @include list-reset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 80%;

  & h3 {
    @include indent;
    font-family: $font-roboto;
    color: $blue;
    font-size: 24px;
    line-height: 34px;
    margin-bottom: 27px;
  }
}

.advantages__list p {
  @include indent;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 40px;
}

.advantages__item {
  position: relative;
  width: 45%;
  padding-left: 25px;
  padding-right: 20px;
  padding-top: 55px;
  min-height: 250px;
  margin-right: 25px;
  margin-bottom: 32px;
  border: 1px solid $white-grey-line;
  border-left: 2px solid $blue;

  &:hover h3,
  &:focus h3 {
    color: $white;
  }

  &:hover,
  &:focus {
    color: $white;
    background-color: $blue;
  }

  & svg {
    fill: $blue;
    margin-bottom: 27px;
  }

  &:hover svg,
  &:focus svg {
    fill: $white;
  }

  p {
    width: 100%;
  }
}

//Mobile//
@media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
  .advantages__item {
    width: 100%;
    margin-right: 0;
  }
}
